const refs = {
  clearBtn: document.getElementById('clear'),
  defaultBtn: document.getElementById('default'),


  decipherBtn: document.getElementById('decipher'),
  encryptBtn: document.getElementById('encrypt'),

  text: document.getElementById('text'),
  result: document.getElementById('result'),
}

const alphabet = [
  'а', 'б', 'в', 'г', 'ґ', 'д',
  'е', 'є', 'ж', 'з', 'и', 'і',
  'ї', 'й', 'к', 'л', 'м', 'н',
  'о', 'п', 'р', 'с', 'т', 'у',
  'ф', 'х', 'ц', 'ч', 'ш', 'Щ',
  'ь', 'ю', 'я'
];

const simpleData = [
  'Ти подобаєшся мені 🤗',
  'У тебе гарні дуже щічки, особливо коли вони стають рожеві 😊',
  'Ти маєш чарівний сміх 😁',
  'Твій шепіт crazy my mind 😏',
  'Від твоєї посмішки не можна відвести очей 😋',
  'У тебе красиве волосся 😃',
  'Твій погляд зачаровує, особливо блиск в очах 🤩'
];

let simpleIndex = 0;

const step = 3;



refs.clearBtn.addEventListener('click', clearText);
refs.defaultBtn.addEventListener('click', setDefault);
refs.decipherBtn.addEventListener('click', getDecipher);
refs.encryptBtn.addEventListener('click', getEncrypt);

function getDecipher() {
  const data = refs.text.value
    .toLowerCase()
    .split('')
    .map(item => {

      const currentIndex = alphabet.findIndex(letter => letter === item);

      console.log('currentIndex', currentIndex);

      if (currentIndex !== -1) {
        const newPos = currentIndex - step;

        if (newPos >= 0) {
          return alphabet[newPos]
        } else {
          // console.log(newPos)

          console.log('newPos', newPos);

          console.log('alphabet.length', alphabet.length)

          const newStep = Math.abs(Math.abs(newPos) - (alphabet.length));
          console.log('newStep',newStep)
          return alphabet[newStep]
        }

      } else return item;
    }).join('')


  renderResult(data);
}

function getEncrypt() {
  const data = refs.text.value
    .toLowerCase()
    .split('')
    .map(item => {

      const currentIndex = alphabet.findIndex(letter => letter === item);

      if (currentIndex !== -1) {
        const newPos = currentIndex + step;

        if (newPos <= alphabet.length - 1) {
          return alphabet[newPos]
        } else {
          const newStep = newPos - alphabet.length;
          return alphabet[newStep]
        }

      } else return item;
    }).join('')


  renderResult(data);
}

function clearText() {
  refs.text.value = '';
  refs.result.innerText = 'Результат буде тут';
}

function setDefault() {

  simpleIndex += 1;
  let text = '';

  if (simpleIndex < simpleData.length) {
    text = simpleData[simpleIndex]
  } else {
    simpleIndex = 0;
    text = simpleData[simpleIndex]
  }

  refs.text.value = text;
}

function renderResult(data) {
  console.log(data);
  refs.result.innerText = data[0]?.toUpperCase() + data?.slice(1);
}

